.services_container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 3rem;
}
.service{
    background: var(--color-bg-varient);
    border-radius: 0.7rem 0.7rem 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);
}
.service:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.service_head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0.7rem 0.7rem 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0,0,0,0.1);
}
.service_head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
    font-weight: 600;
    margin-top: -1rem;
}
.service_head h4{
    color: var(--color-bg);
    font-size: 0.8rem;
    text-align: center;
    margin-bottom: -1rem;
}
.service_list{
    padding: 2rem;
}
.service_list li{
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service_list-icon{
    color: var(--color-primary);
}
.service_list p{
    font-size: 0.9rem;
}
.service_list h5{
    font-size: 0.8rem;
    color: var(--color-primary);
}
/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    .services_container{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    .services_container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}