header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* cta */
.cta{
    margin-top: 2.3rem;
    display: flex;
    justify-content: center;
    gap: 1.2rem;
}
/* socials */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 3px;
    height: 3rem;
    background: var(--color-primary);
}
/* image */
.me{
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem;   */
}
/* scroll bar */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}
/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header_socials,
    .scroll_down{
        display: none;
    }
}