@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&family=Potta+One&family=Roboto:wght@300;400;500;700&display=swap);
header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* cta */
.cta{
    margin-top: 2.3rem;
    display: flex;
    justify-content: center;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
/* socials */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 3px;
    height: 3rem;
    background: var(--color-primary);
}
/* image */
.me{
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem;   */
}
/* scroll bar */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}
/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header_socials,
    .scroll_down{
        display: none;
    }
}
.about_container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}
.about_me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(30deg,transparent,var(--color-primary),transparent
    );
    display: grid;
    place-items: center;
}
.about_me-image{
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}
.about_me-image:hover{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
.about_cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.about_card{
    background: var(--color-bg-varient);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}
.about_card:hover{
    background:transparent;
    border-color:var(--color-primar-variant);
    cursor: pointer;
}
.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about_card h5{
    font-size: 0.95rem;
}
.about_card small{
    font-size: 0.8rem;
    color: var(--color-light);
}
.about_content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    .about_container{
       grid-template-columns: 1fr;
       grid-gap: 0;
       gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p{
        margin:1.5rem 0.5rem;
    }
}
.container.contact_container{
    width: 50%;
    display: grid;
    grid-template-columns: 40% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact_options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
.contact_option{
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}
.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
contact_option-icon{
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.9rem;
}
.contact_option h5{
    font-size:1rem ;
    font-weight: 200;
}
/* Form styling */
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}
input,textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background:transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color:var(--color-white);
    font-size: 1rem;
}
input{
    height: 10%;
}
form button{
    font-size: 1rem;
}
/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    .container.contact_container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    .container.contact_container{
        width: var(--container-widht-sm);
    }
}
.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}
.experience_container > div{
    background: var(--color-bg-varient);
    padding: 1rem 4rem;
    border-radius: 2rem;
    border: 1px solid transparent;

}
.experience_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: pointer;
}
.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
    font-weight: 600;
}
.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    grid-column-gap: 3rem;
    -webkit-column-gap: 3rem;
            column-gap: 3rem;
}
.experience_details{
    display: flex;
   grid-gap: 0.5rem;
   gap: 0.5rem; 
}
.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}
.inline_element{
    text-align: center;
    justify-content: flex-start;
}

/* media-query (medium devices) */
@media screen and (max-width: 1024px) {
    .experience_container{
        grid-template-columns: 1fr;
     }
     .experience_container > div{
         width: 80%;
         padding: 2rem;
         margin: 0 auto;
     }
     .experience_content{
         padding: 1rem;
     }
}

/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    .experience_container{
       grid-gap: 1rem;
       gap: 1rem;
    }
    .experience_container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
    .experience_content{
        padding: 1rem;
    }
}
footer{
    background: var(--color-bg-varient);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-primary);
}
.footer_logo{
    display: flex;
    border: 1px solid var(--color-primary);
    padding: 0 0.5rem;
    border-radius: 1.2rem;
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks{
    font-size: 1.2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer_socials{
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer_socials a{
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    display: flex;
    border: 1px solid transparent;
    border-radius: 0.7rem;
}
.footer_socials a:hover{

    background: var(--color-primary);
    color: black;
}
.footer_copyright{
    margin-bottom: 4rem;
    color: var(--color-primary);
}
/* Media query for mobile phones(Small tablets) */
@media screen and (max-width: 600px) {
    .permalinks{
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
    .footer_socials{
        margin-bottom: 2.6rem;
    }
}
nav{
    background: rgba(0,0,0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    left: 50%;
    position: fixed;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    display: flex;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.7rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-primary);
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0,0,0,0.3);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}

